<template>
<div>
  <div style=" margin-top: 15px;margin-bottom:20px;">
  <el-input placeholder="请输入内容" v-model="searchValue" class="input-with-select">
    <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
  </el-input>
  </div>

  <!-- 学生信息表 -->
  <el-table v-loading="loadShow" :data="tableData" style="width: 100%">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="姓名">
            <span>{{ props.row.realName }}</span>
          </el-form-item>
          <el-form-item label="出生日期">
            <span>{{  new Date(props.row.birth).toLocaleDateString().replace(/\//g, "-") }}</span>
          </el-form-item>
          <el-form-item label="性别">
            <span>{{ props.row.sex }}</span>
          </el-form-item>
          <el-form-item label="邮箱:">
            <span>{{ props.row.email }}</span>
          </el-form-item>
          <el-form-item label="证件照:">
            <span><img :src="props.row.cardPic"/></span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column label="ID" width="360" prop="uid">
    </el-table-column>
    <el-table-column label="昵称" prop="name">
    </el-table-column>
    <el-table-column label="类型">
      <template slot-scope="scope">
        {{scope.row.type < 3 ? '普通用户' : '漫画作者'}}
      </template>
    </el-table-column>
    <el-table-column fixed="right" label="操作" width="100">
      <template slot-scope="scope">
        <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text"  size="small">移除</el-button>
      </template>
    </el-table-column>
  </el-table>

  <div class="pager">
    <el-pagination layout="prev, pager, next" :current-page="currentPage" :total=totalCount @current-change="handleCurrentChange">
    </el-pagination>      
  </div>

</div>
</template>


<script>
  export default {
    data() {
      var validateUserName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入内容！'))
        } else {
          callback();
        }
      }
      return {
        logining: false,
        loadShow: true,
        searchParam: {},
        tableData: [],
        searchValue: '',
        selectItem: '',
        currentPage: 1,
        totalCount: 0
      }
    },
    created(){
      this.getData()
      setTimeout(() => {
        this.loadShow = false
      },600)
    },
    methods: {
      search(){
        this.currentPage = 1
        this.searchParam = {}
        if(this.searchValue !== ''){
          this.searchParam.param = this.searchValue
          this.getData()
        }
      },
      getData(){
        var _this = this
        this.$ajax.post('/api/c/getUsers',_this.searchParam).then(function(response){
          _this.getMsg(_this,response)
        }).catch(function(error){console.log(error);})
      },
      handleCurrentChange(val) {
        this.searchParam.page = val-1
        this.currentPage = val
        this.getData()
      },
      deleteRow(index, rows) {
        var _this = this
        this.$ajax.get('/api/c/removeUser?id='+rows[index].uid).then(function(response){
          _this.callMsg(_this,response)
          setTimeout(function () {
            _this.getData()
          }, 500);
        }).catch(function(error){console.log(error);}); 
        rows.splice(index, 1);
      },
      handleClick(index,row) {
        this.formData=row
      }
    }
  }
</script>



<style >
.pager{margin-top:35px;text-align:center;}
.demo-table-expand {font-size: 0;}
.demo-table-expand label {width: 90px;color: #99a9bf;}
.demo-table-expand .el-form-item {margin-right: 0;margin-bottom: 0;width: 50%;}
@media (max-width: 480px){
.add{font-size: 42px;right: 6px;top:0;margin-bottom: 43px;}
}
</style>


